.header__container, .main__container {
  margin: 0 auto;
  max-width: 1632px;
  padding: 0 16px; }

@keyframes carret-appearance {
  from {
    height: 0; }
  to {
    height: 100%; } }

@keyframes dilutionIn {
  from {
    filter: blur(5px);
    opacity: 0;
    transform: scale(0.61803); }
  to {
    filter: blur(0);
    opacity: 1;
    transform: scale(1); } }

.header {
  background-color: #f7d14c; }
  .header__row {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .header__row:not(:empty) {
      margin: 0 -16px; }
    .header__row > * {
      padding: 0 16px; }
  .header__logo {
    color: #000;
    font-size: 19.0985px;
    font-weight: 700;
    letter-spacing: 1px;
    line-height: 1;
    margin: 0 auto;
    padding-bottom: 16px;
    padding-top: 16px;
    text-decoration: none;
    white-space: nowrap; }
    @media (min-width: 320px) {
      .header__logo {
        font-size: calc(50px + (30.9015) * ((100vw - 1600px) / 1000)); } }
    @media (min-width: 1600px) {
      .header__logo {
        font-size: 50px; } }
  .header__menu {
    align-self: stretch;
    flex-grow: 1; }

.menu {
  display: flex;
  flex-direction: column; }
  .menu__row {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 14.63572px;
    position: relative; }
    @media (min-width: 320px) {
      .menu__row {
        min-height: calc(124px + (109.36428) * ((100vw - 1600px) / 1280)); } }
    @media (min-width: 768px) {
      .menu__row:not(:empty) {
        margin: 0 -8px; } }
    @media (min-width: 1600px) {
      .menu__row {
        min-height: 124px; } }
  .menu__link {
    align-items: center;
    color: #000;
    display: flex;
    flex-grow: 1;
    font-size: 10.31319px;
    font-weight: 700;
    font-weight: 400;
    justify-content: center;
    line-height: 1.07667;
    padding: 0.61803em 8px;
    text-decoration: none; }
    .menu__link--lead {
      font-weight: 700; }
    @media (min-width: 320px) {
      .menu__link {
        font-size: calc(27px + (16.68681) * ((100vw - 1600px) / 1280)); } }
    @media (min-width: 1600px) {
      .menu__link {
        font-size: 27px; } }
  .menu__carret {
    animation-delay: .3s;
    animation-duration: .3s;
    animation-fill-mode: backwards;
    animation-name: carret-appearance;
    animation-timing-function: ease-out;
    background-color: #fff;
    height: 100%;
    left: 0;
    position: absolute;
    transition-duration: .3s;
    transition-property: left, width;
    transition-timing-function: ease-out; }
    .menu__carret-rails {
      align-items: flex-end;
      bottom: 0;
      display: flex;
      height: 0;
      left: 0;
      position: absolute;
      right: 0; }
      @media (min-width: 768px) {
        .menu__carret-rails {
          height: calc(22px + (13.59666) * ((100vw - 1600px) / 1280)); } }
      @media (min-width: 1600px) {
        .menu__carret-rails {
          height: 22px; } }

.main__container {
  padding-bottom: 16px;
  padding-top: 16px; }

.main__features {
  animation-delay: .8s;
  animation-duration: 1s;
  animation-fill-mode: backwards;
  animation-name: dilutionIn;
  animation-timing-function: ease-out;
  transform-origin: center left; }

.features {
  table-layout: fixed;
  width: 100%; }
  .features__item:not(:last-child) > td,
  .features__item:not(:last-child) > th {
    padding-bottom: 28.64775px; }
    @media (min-width: 320px) {
      .features__item:not(:last-child) > td,
      .features__item:not(:last-child) > th {
        padding-bottom: calc(75px + (46.35225) * ((100vw - 1600px) / 1000)); } }
    @media (min-width: 1600px) {
      .features__item:not(:last-child) > td,
